.gallery {
  background-image: linear-gradient(180deg, #fff5f6 0% 20%, #fff 20% 100%);
  // padding-bottom: 60px;
}

.gallery-list {
  display: grid;
  grid-gap: 3px;
  grid-template-rows: repeat(15, 1fr);
  grid-template-columns: repeat(7, 1fr);
  margin-left: auto;
  margin-right: auto;
}

.gallery-list img {
  height: 100%;
  width: 100%;
  border-radius: 12%;
  margin: 0;
  position: relative;
  overflow: hidden;
}

.gallery-item-1 {
  grid-area: 6/1/11/2;
}

.gallery-item-2 {
  grid-area: 4/2/9/3;
}

.gallery-item-3 {
  grid-area: 9/2/14/3;
}

.gallery-item-4 {
  grid-area: 1/3/11/5;
}

.gallery-item-5 {
  grid-area: 11/3/16/4;
}

.gallery-item-6 {
  grid-area: 5/5/10/6;
}

.gallery-item-7 {
  grid-area: 10/5/15/6;
}

.gallery-item-8 {
  grid-area: 3/6/8/7;
}

.gallery-item-9 {
  grid-area: 8/6/13/7;
}

.gallery-item-10 {
  grid-area: 6/7/11/8;
}

@media screen and (max-width: 1279px) {
  .gallery-picture.container {
    padding: 0;
  }
}

@media screen and (min-width: 1280px) {
  .gallery-picture.container {
    padding: 0 62px;
    padding-bottom: 120px;
  }
}

@media screen and (max-width: 1279px) {
  .gallery {
    // padding-bottom: 60px;
  }
}

