:root {
  --main-font-family: "DM Sans", sans-serif;
  --secondary-font-family: "Titan One", cursive;
  --main-text-theme-bright: #fff;
  --main-text-theme-light: #000;
  --secondary-text-theme-light: #907e82;
  --accent-text-dark: #d41443;
  --accent-text-light: #ffa5ba;
  --background-white: #e5e5e5;
  --background-hero: #ffb8ca;
  --background-light-pink: #fff5f6;
  --background-coffee: #f0d1a5;
  --background-milkshakes: #c2e297;
  --background-mobile-menu: #e17992;
  --hover-btn-light: #ecebeb;
  --hover-btn-dark: #cf4c6c;
  --element-divider: #e1e1e1;
  --mobile-separator: #e18298;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

body {
  font-family: var(--main-font-family);
  margin: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

button {
  cursor: pointer;
  border: 0;
}

input {
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.mobile-menu {
  z-index: 99;
  background-color: var(--background-mobile-menu);
  width: 250px;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  padding: 60px 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  overflow: scroll;
  transform: translateX(100%);
  box-shadow: -8px 0 20px rgba(0, 0, 0, .25);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0%);
}

.mobile-menu-container {
  padding: 0 20px;
}

.mobile-close-btn {
  z-index: 999;
  background-color: rgba(0, 0, 0, 0);
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-icon {
  stroke: var(--main-text-theme-bright);
  transition: box-shadow .25s linear;
}

.mobile-close-btn:hover .close-icon, .mobile-close-btn:focus .close-icon {
  box-shadow: 2px 3px 3px var(--accent-text-dark);
}

.mobile-modal-btn {
  height: 44px;
  color: var(--accent-text-dark);
  background-color: var(--main-text-theme-bright);
  width: 100%;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;
  padding: 0 48px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  transition: background-color .25s linear;
  display: flex;
}

.mobile-modal-btn:hover, .mobile-modal-btn:focus {
  background-color: var(--hover-btn-light);
}

.modal-btn-icon {
  margin-left: 16px;
}

.mobile-menu-item {
  border-top: 1px solid var(--mobile-separator);
}

.mobile-menu-item:last-child {
  border-bottom: 1px solid var(--mobile-separator);
}

.mobile-menu-link {
  color: var(--main-text-theme-bright);
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28571;
  transition: color .25s linear;
  display: block;
}

.mobile-menu-link:hover, .mobile-menu-link:focus, .mobile-menu-link.active {
  color: var(--accent-text-dark);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: 40vw;
  }

  .mobile-modal-btn {
    width: auto;
  }
}

@media screen and (min-width: 1280px) {
  .mobile-menu {
    display: none;
  }
}

.modal-btn {
